<template>
  <div class="black-bg" v-if="modalState">
    <div class="white-bg">
      <h4>{{ products[productNum].title }}</h4>
      <img :src="products[productNum].image" />
      <!-- <input @input="month = $event.target.value">-->
      <input v-model="month">
      <p> {{ month }}개월 선택함 : {{ products[productNum].price * month}}</p>
      <p>{{ products[productNum].content }}</p>
      <button @click="close()">닫기</button> <!-- props는 read-only임, 받아온거 수정하면 안됨-->
      <!-- 할인배너 -->
      <Discount/>
    </div>
  </div>
</template>

<script>
export default {
  name: "Modal",
  data(){
    return {
      month: 1
    }
  },
  props: {
    products: Array,
    productNum: Number,
    modalState: Boolean
  },
  methods: {
    close() {
      this.$emit('closeModal');
    }
  },
  watch: {
    month(val) {   // (afVal, bfVal) 변경전데이터 변경후 데이터
      if (val.indexOf(' ')>-1) {
        alert('경고 : 숫자만 입력하세요');
        this.month = 1;
      }
      if(isNaN(val)) {
        alert('경고 : 숫자만 입력하세요');
        this.month = 1;
      }
      if(val>12) {
        alert('경고:13이상 입력하지 마세요');
        this.month = 1;
      }
    }
  },
  updated() {
    if(this.month==2) {
      alert('3개월 부터 입력 가능합니다.');
    }
  }
}
</script>

<style scoped>
  body {
    margin: 0
  }
  div {
    box-sizing: border-box;
  }
  .black-bg {
    width:100%; height: 100%;
    background: rgba(0,0,0,0.5);
    position: fixed; padding: 20px;
  }
  .white-bg {
    width: 100%; background: white;
    border-right: 8px;
    padding: 20px;
  }
</style>