<template>

  <!--  모달 애니메이션 방법1
  <div class="start" :class="{ end : modalState }">
    <Modal @closeModal="modalState = false" :modalState="modalState" :products="products" :productNum="productNum"  />
  </div>
  -->
  <!--  모달 애니메이션 방법2 -->
  <transition name="fade">
    <Modal @closeModal="modalState = false" :modalState="modalState" :products="products" :productNum="productNum"  />
  </transition>


  <div class="menu">
    <a v-for="(menuName, i) in menu" :key="i">{{ menuName }}</a>
  </div>

  <!-- 할인배너 -->
  <Discount :discountNum="discountNum"/> <!--  <Discount></Discount> 같은 방법-->
  <!--// 할인배너 -->

  <button @click="priceSort(i)" v-for="(sortName,i) in sortNames" :key="i">{{ sortName }}</button>
  <button @click="sortBack">되돌리기</button>
  <!-- 상품목록 -->
  <!-- 자식컴포넌트에서 부모컴포넌트 데이터 변경시 사용하는 것들  -->
  <Card @openModal="modalState = true; productNum = $event" v-for="(product, i) in products" :key="i" :product="product" :i="i" :h4style="h4style"/>
  <!--// 상품목록 -->
</template>

<script>

import data from './data';
import Discount from './Discount';
import Modal from './Modal';
import Card from './Card';
//import {data, tmpData} from './data.js';
//import {data} from './data.js';

export default {
  name: 'App',
  data(){         // 데이터보관함 object {} 형식
    return {
      sortNames: ['가격낮은순정렬', '가격높은순정렬', '가나다순정렬'],
      menu: ['Home', 'Product', 'About'],
      modalState: false, // true: 열림, false: 닫힘
      productNum: 0,     // 사용자가 누른 집 index
      h4style: 'color:blue',
      productsOrigin : [...data],
      products : data,
      showDiscount: true,
      discountNum: 30
    }
  },
  methods: {
    increase(i){
      this.신고수[i]++;
    },
    openModal(i) {
      this.modalState = true;
      this.productNum = i;
    },
    priceSort(i) {
      if (i == 0) {
        this.products.sort(function(a, b){
          return a.price -b.price
        });
      } else if (i == 1) {
        this.products.sort(function(a, b){
          return b.price -a.price
        });
      } else {
        this.products.sort(function(a, b){
          return a.title.type.toLowerCase() < b.title.type.toLowerCase()
        });
      }
    },
    sortBack() {
      this.products = [...this.productsOrigin];  // 등호로 쓰면 값을 공유해주세요의 뜻, ... 붙이면 개별 복사본 만듬 [...array자료]
    }
  },
  mounted() {
    setTimeout(()=>{
      this.showDiscount = false;
    }, 200);

    setInterval(()=>{
      this.discountNum--;
    },300);
  },
  components: {
  //  Discount: Discount
    Discount,    // 왼쪽 오른쪽 이름이 같으면 하나만 쓸수 있음 es6 문법임
    Modal,
    Card
  }
}
</script>

<style>
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
  .menu {
    background: darkslateblue;
    padding: 15px;
    border-radius: 5px;
  }
  .menu a {
    color: white;
    padding: 10px;
  }
  .start {
    opacity: 0;
    transition: all 1s;
  }
  .end {
    opacity: 1;
  }
  .fade-enter-from {
    transform: translateY(-1000px);
  }
  .fade-enter-active {
    transition: all 1s;
  }
  .fade-enter-to {
    transform: translateY(0px);
  }
  .fade-leave-from {
    opacity: 1;
  }
  .fade-leave-active {
    transition: all 1s;
  }
  .fade-leave-to {
    opacity: 0;
  }
</style>
